.team {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 29px 16px 58px;
  display: flex;
}

.team__card-description {
  color: #2e3033;
  text-align: center;
  text-transform: uppercase;
  flex-direction: column;
  align-items: center;
  font-family: Raleway, sans-serif;
  display: flex;
}

.team__card-name {
  align-items: center;
  height: 50px;
  margin: 0;
  padding: 0;
  font-size: 18px;
  font-weight: 700;
  display: flex;
}

.team__card-position {
  align-items: center;
  height: 20px;
  margin: 0;
  padding: 0;
  font-size: 14px;
  font-weight: 400;
  display: flex;
}

.team__text {
  margin-bottom: 18px;
}

.team__card {
  flex-direction: column;
  margin-bottom: 30px;
  display: flex;
  position: relative;
}

.team__card:last-of-type {
  margin-bottom: 0;
}

.team__card-img.active .social-media__icons {
  opacity: 1;
  transition: opacity .3s linear;
}

.social-media__icons {
  opacity: 1;
  transition: opacity .3s linear;
  position: absolute;
  top: 194px;
  right: 0;
}

.team__card-img {
  position: relative;
}

.team__card-img-big {
  display: none;
}

.social-media__icons a {
  color: inherit;
  text-decoration: none;
}

@media (width >= 768px) {
  .team__text {
    max-width: 992px;
    margin-bottom: 50px;
  }

  .team__card-container {
    flex-flow: wrap;
    justify-content: center;
    gap: 30px 20px;
    display: flex;
  }

  .team__card {
    margin-bottom: 0;
    position: relative;
  }

  .team__card-svg {
    width: 35px;
    height: 35px;
  }

  .team__card-img-big {
    width: 318px;
    height: 360px;
    display: flex;
  }

  .team__card-img {
    display: none;
  }

  .team__card-img-wrap {
    position: relative;
  }

  .team__card-img-wrap:after {
    content: "";
    z-index: 5;
    pointer-events: none;
    opacity: 0;
    background: linear-gradient(360deg, #edd789e6 0%, #e98283e6 100%);
    width: 316px;
    height: 360px;
    transition: opacity .3s linear;
    position: absolute;
    inset: -4px 0 0 -3px;
  }

  .team__card:hover .team__card-img-wrap:after {
    opacity: 1;
  }

  .team__card.active .team__card-img-wrap:after {
    content: "";
    z-index: 10;
    pointer-events: none;
    background: linear-gradient(360deg, #edd789e6 0%, #e98283e6 100%);
    width: 316px;
    height: 360px;
    position: absolute;
    inset: -4px 0 0 -3px;
  }

  .social-media__icons {
    opacity: 0;
    top: 166px;
    right: 88px;
  }

  .team__card.active .social-media__icons {
    opacity: 1;
    z-index: 10;
    transition: opacity .3s linear;
    position: absolute;
    top: 166px;
    right: 88px;
  }

  .team__card:hover .social-media__icons {
    opacity: 1;
    z-index: 10;
    top: 166px;
    right: 88px;
  }
}

@media (width >= 1000px) {
  .team__card-container {
    flex-wrap: nowrap;
  }
}

@media (width >= 1300px) {
  .team {
    padding: 84px 0;
  }

  .team__text {
    max-width: 1170px;
    height: 118px;
    margin-bottom: 58px;
  }

  .team__card-container {
    column-gap: 30px;
  }

  .team__card-img-big {
    width: 370px;
    height: 424px;
  }

  .team__card-svg {
    width: 42px;
    height: 42px;
  }

  .team__card:hover .team__card-img-wrap:after, .team__card.active .team__card-img-wrap:after {
    width: 370px;
    height: 424px;
    top: -16px;
    left: -16px;
  }

  .team__card.active .social-media__icons, .team__card:hover .social-media__icons {
    top: 198px;
    right: 100px;
  }

  .team__card-name {
    height: 80px;
    font-size: 24px;
  }

  .team__card-position {
    height: 30px;
    font-size: 18px;
  }
}
